<template>
<div class="ContactForm">
    <input v-model="name" class="ContactForm__input" type="name" placeholder="Your name">
    <input v-model="email" class="ContactForm__input" type="email" placeholder="Your email">
    <textarea v-model="text" class="ContactForm__textarea" name="message" id="message" placeholder="Leave us a message"></textarea>
    <button class="button" @click="submitForm">Submit</button>
</div>
</template>

<script>
import axios from 'axios'
export default {
    name: 'form-contact',
    data () {
        return {
            email: '',
            text: '',
            name: ''
        }
    },
    methods: {
        submitForm () {
            axios.create()
                .post('/email', {
                    email: this.email,
                    text: this.text
                })
                .then((res) => {
                    console.log('data: ', res)
                })
                .catch((error) => {
                    console.log('error: ', error)
                })
        }
    }
}
</script>
